/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { usePortal } from "Hooks"
import { FilterHook, findPortalElement } from "Utils"
import { hasLink } from "Common/settings"
import { useItemsState } from "../../../items/_state/hooks"
import { useProductState } from "../_state/hooks"

const Link = wp.element.lazy(() =>
  import(/* webpackChunkName: 'Link-public' */ "../../../link")
)

function ProductTitle() {
  const itemsState = useItemsState()
  const productState = useProductState()

  const titleStyles = css`
    && {
      font-family: ${itemsState.settings.titleTypeFontFamily
        ? itemsState.settings.titleTypeFontFamily
        : "inherit"};
      font-weight: ${itemsState.settings.titleTypeFontWeight
        ? itemsState.settings.titleTypeFontWeight
        : "initial"};
      font-size: ${itemsState.settings.titleTypeFontSize
        ? itemsState.settings.titleTypeFontSize
        : itemsState.settings.titleSize};
      letter-spacing: ${itemsState.settings.titleTypeLetterSpacing
        ? itemsState.settings.titleTypeLetterSpacing
        : "initial"};
      line-height: ${itemsState.settings.titleTypeLineHeight
        ? itemsState.settings.titleTypeLineHeight
        : "initial"};
      text-decoration: ${itemsState.settings.titleTypeTextDecoration
        ? itemsState.settings.titleTypeTextDecoration
        : "initial"};
      text-transform: ${itemsState.settings.titleTypeTextTransform
        ? itemsState.settings.titleTypeTextTransform
        : "initial"};
      font-style: ${itemsState.settings.titleTypeFontStyle
        ? itemsState.settings.titleTypeFontStyle
        : "initial"};

      color: ${itemsState.settings.titleColor};
      white-space: normal;
      margin: 0;
    }
  `

  const titleStylesWrapper = css`
    margin-bottom: ${itemsState.settings.isSingleComponent ? "0px" : "10px"};
  `

  return usePortal(
    <div
      className="wps-component wps-component-products-title"
      aria-label="Product Title"
      data-wps-component-order="0"
      css={titleStylesWrapper}
    >
      {hasLink(itemsState.settings) ? (
        <Link
          type="products"
          payload={productState.payload}
          target={itemsState.settings.linkTarget}
          linkTo={itemsState.settings.linkTo}
        >
          <Title
            styles={titleStyles}
            title={productState.payload.title}
            classList={itemsState.settings.titleClassName}
            product={productState.payload}
          />
        </Link>
      ) : (
        <Title
          styles={titleStyles}
          title={productState.payload.title}
          classList={itemsState.settings.titleClassName}
          product={productState.payload}
        />
      )}
    </div>,
    findPortalElement(itemsState.settings.dropzoneProductTitle)
  )
}

function Title(props) {
  const CustomHeading = `h${shopwp.misc.isSingularProducts ? "1" : "2"}`

  return (
    <>
      <FilterHook name="before.productTitle" args={[props]} />

      <CustomHeading
        itemProp="name"
        className={props.classList}
        css={props.styles}
      >
        {wp.hooks.applyFilters("product.titleText", props.title, props.product)}
      </CustomHeading>

      <FilterHook name="after.productTitle" args={[props]} />
    </>
  )
}

export default wp.element.memo(ProductTitle)
